import JHTAJAX from "./JHTAjax";
import JHT from "./JHT";
import {BASEPATH} from "./Enum";
import MyToast from "../components/MyToast";
let wx = require('weixin-js-sdk');

export default class ThirdSDK extends JHT{
    constructor(wxJsList = [],debug = false){
        super();
        this.myToast = new MyToast();
        if (this.working() === "WX"){
            this.sdkInit(wxJsList,debug);
        }else if(this.working() === "ZFB"){

        }
    }
    sdkInit(wxJsList = [],debug = false){
        JHTAJAX({
            url:`${BASEPATH.jspsnURL}/signature.servlet`,
            data: {
                url:location.href.split("#")[0],
                appType:window.USERINFO?USERINFO.APP.APP_TYPE : (this.urlParams().APP_TYPE?this.urlParams().APP_TYPE:"WX_JTC")
            },
            timeOut:10000,
            type:'post',
            dataType:'json',
            async:"false",
            success:function(temp){
                if (temp != null) {
                    let sAppId = temp.appId;
                    let iTimestamp =  temp.timestamp;
                    let sNonceStr = temp.noncestr;
                    let sSignature = temp.signature;
                    wx.config({
                        debug: debug || false, // ,apialertpclogpc
                        appId: sAppId, // 
                        timestamp: iTimestamp, // 
                        nonceStr: sNonceStr, // 
                        signature: sSignature, // 1
                        jsApiList: wxJsList
                    });
                    wx.error(function(msg){
                        this.myToast.setToast(msg.errMsg);
                    }.bind(this));
                }else{
                    this.setToast.setToast('!');
                }
            }.bind(this),
            complete:function(data){
            },
            error:function(error){
                console.log(error);
            }
        });
    }
    setMenu(items = [],op = "hide"){
        wx.ready(function(){
            if(op == "show"){
                wx.showMenuItems({
                    menuList:items // menu3
                });
            }else if(op == "showAll"){
                wx.showAllNonBaseMenuItem();
            }else if(op == "hide"){
                wx.hideMenuItems({
                    menuList: [] // menu3
                });
            }else if(op == "hideAll"){
                wx.hideAllNonBaseMenuItem();
            }else if(op == "close"){
                wx.closeWindow();
            }
        });
        if(op == "close"){
            wx.closeWindow();
        }
    }
    wxCall( op={}){
        if(!op.serviceId){
            this.myToast.setToast("serverIdJS-SDK");
            return false;
        }else if(typeof op.serviceId !== "object"){
            this.myToast.setToast("serverIdJS-SDK");
            return false;
        }
        if (this.working() === "WX"){
            wx.ready(function(){
                for (let i in op.serviceId){
                    switch (op.serviceId[i]){
                        case "onMenuShareQQ":
                            shareQQ(op);
                            break;
                        case "onMenuShareWeibo":
                            shareWeibo(op);
                            break;
                        case "onMenuShareAppMessage":
                            shareWX(op);
                            break;
                        case "onMenuShareTimeline":
                            shareTimeline(op);
                            break;
                        case "onMenuShareQZone":
                            shareQZone(op);
                            break;
                        case "scanQRCode":
                            scanQRCode(op);
                            break;
                        case "getLocation":
                            getLocation(op);
                            break;
                        default:
                            return false;
                    }
                }

            }.bind(this));
            wx.error(function(msg){
                if (op.error && typeof op.error === "function")
                    op.error(msg);
            });
        }else if(jht.working() === "ZFB"){

        }else if(jht.working() === "APP"){

        }

    }
}
function shareQQ(obj){
    let success = obj.success;
    let cancel = obj.cancel;
    wx.onMenuShareQQ({
        title: obj.title, // 
        desc: obj.desc, // 
        link: obj.link, // 
        imgUrl: obj.imgUrl, // 
        success: function () {
            // 
            if (success && typeof success === "function")
                success();
        },
        cancel: function () {
            // 
            if (cancel && typeof cancel === "function")
                cancel();
        }
    });
}
function shareWX(obj){
    let success = obj.success;
    let cancel = obj.cancel;
    let fail = obj.fail;
    let trigger = obj.trigger;
    wx.onMenuShareAppMessage({
        title: obj.title,
        desc:  obj.desc,
        link: obj.link,
        imgUrl: obj.imgUrl,
        trigger: function (res) {
            // triggerajaxajax
            // jsLifeKFS.poptip('');
            if (trigger && typeof trigger === "function")
                trigger(res);
        },
        success: function (res) {
            if (success && typeof success === "function")
                success(res);
            //jsLifeKFS.poptip("");
        },
        cancel: function (res) {
            if (cancel && typeof cancel === "function")
                cancel(res);
        },
        fail: function (res) {
            console.log(JSON.stringify(res));
            if (fail && typeof fail === "function")
                fail(res);
        }
    });
}
