function scanQRCode(op){
    let needResult = op.needResult || 1;
    let scanType = op.needResult || ["qrCode","barCode"];
    let success = op.success;
    wx.scanQRCode({
        needResult: needResult, // 01
        scanType: scanType, // 
        success: function (res) {
            result(res);
            /*var result = res.resultStr; // needResult  1 
            if (success && typeof success === "function")
                success(res);*/
        }
    });
    function result(res) {
        var result = res.resultStr; // needResult  1 
        if (success && typeof success === "function")
            success(res);
        else
        return result;
    }
}
function getLocation(op) {
    let type = op.type || 'wgs84';
    let success = op.success;
    wx.getLocation({
        type: type || 'wgs84', // wgs84gpsopenLocation'gcj02'
        success: function (res) {
            var latitude = res.latitude; // 90 ~ -90
            var longitude = res.longitude; // 180 ~ -180
            var speed = res.speed; // /
            var accuracy = res.accuracy; // 
            result(res);
        }
    });
    function result(res) {
        if (success && typeof success === "function")
            success(res);
        else
            return res;
    }
}


