import {EXCLUDEPAGE, TXTTOIMG,BASEPATH} from "./Enum";
import MyToast from "../components/MyToast";
export default class JHT {
    constructor(){
    }
    basePath(){
        return BASEPATH;
    }
    /******  URL  *************/
    urlParams () {
        let str = location.search.length > 0 ? location.search.substring(1) : "";
        let items = str.length ? str.split("&") : [];
        let args = {},
            item = null,
            name = null,
            value = null;
        for (let i = 0, len = items.length; i < len; i++) {
            item = items[i].split("=");
            name = decodeURIComponent(item[0]);
            value = decodeURIComponent(item[1]);
            if (name.length) {
                args[name] = value;
            }
        };
        return args;
    };
    /**
     *
     * @param el   DOM
     * @param parem
     *origin
     *name 
     *destination
     *modedriving
     *region
     */
    goNavigation(parem){
            let gotohere = `http://api.map.baidu.com/direction?origin=latlng:${parem.slat},${parem.slng}|name:${parem.sname}&destination=latlng:${parem.elat},${parem.elng}|name:${parem.ename}&mode=${parem.mode || 'driving' }&coord_type=${parem.coordType || "gcj02"}&zoom=17&region=${parem.region || ""}&output=${parem.output || "html"}&src=JTC`;
            window.open(encodeURI(gotohere)); //
        //let gotohere =`http://api.map.baidu.com/direction?origin=latlng:lat +","+lng+"|name:"+city+"&destination=latlng:"+ parem.latitude +","+parem.longitude+'|name:'+data.attributes.name+'&mode=driving&region=&output=html&src=yourCompanyName|yourAppName`;
    }
    /**
     * 
     * Date Date String
     * (M)(d)(h)(m)(s)(q)  1-2 
     *  (y) 1-4 (S) 1 ( 1-3 )
     *  
     *  (new Date()).Format("yyyy-MM-dd HH:mm:ss.S") ==> 2006-07-02 08:09:04.423
     *  (new Date()).Format("yyyy-M-d H:m:s.S")      ==> 2006-7-2 8:9:4.18
     */
    dateFmtInit(){
        Date.prototype.Format = function (fmt) {
            var o = {
                "M+": this.getMonth() + 1, //
                "d+": this.getDate(), //
                "H+": this.getHours(), //
                "m+": this.getMinutes(), //
                "s+": this.getSeconds(), //
                "q+": Math.floor((this.getMonth() + 3) / 3), //
                "S": this.getMilliseconds() //
            };
            if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
            for (let k in o)
                if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt;
        }
    }

    /**
     *
     * @returns {boolean}
     */
    exclude(excludepage = EXCLUDEPAGE){
        for (let i in excludepage){
            if(window.location.href.indexOf(excludepage[i]) > -1){
               return true;
            }
        }
        return false;
    }

    /**
     * 
     * @param str  
     * @param long 
     * @returns {string}
     */
    getStrSub(str = "",long=12) {
        let myLen = 1;
        let _str='';
        let i = 0;
        long = 2*(long +1);
        if (str == '') {
            _str == '--';
        }else {
            for (; i < str.length; i++) {
                if (str.charCodeAt(i) > 0 && str.charCodeAt(i) < 128) {
                    myLen++;
                    if(myLen<long)
                        _str += str.charAt(i);
                } else  {
                    myLen += 2;
                    if(myLen<long)
                        _str += str.charAt(i);
                }
            }
            if (myLen >= long && _str.indexOf('...') == -1) {
                _str += '...';
            }
        }
        return _str;
    }
    /****H5**********************/
    working(app = ""){
        let ua = window.navigator.userAgent.toLowerCase();
        if(ua.match(/MicroMessenger/i) == 'micromessenger'){
            return "WX";
        }else if(ua.match(/AlipayDefined/i) == 'alipaydefined'){  //
            return "ZFB";
        }else if((window.USERINFO &&
            (window.USERINFO.APP.APP_TYPE == "APP_JTC" ||  window.USERINFO.APP.APP_TYPE == "IOS_JTC")) ||
            this.urlParams().APP_TYPE ){
            if(window.USERINFO &&window.USERINFO.APP && app != ""){
                if( window.USERINFO.APP.APP_TYPE == "APP_JTC" ){
                    return "ANDROID";
                } else if( window.USERINFO.APP.APP_TYPE == "IOS_JTC" ){
                    return "IOS_JTC";
                }
            }
            return "APP";
        }else {
            return "OTHER";
        }
    }

    /**
     * H5
     */
    telephone(mobileno = ""){
        if(this.working("app") == "ANDROID"){  //app
            AppBridge.forJS({
                serviceId:"CALLPHONE",
                params:{
                    mobileno: mobileno
                }
            },function(data){
            })
        }else if (this.working("app") == "IOS_JTC") {
            AppBridge.forJS({
                serviceId:"CALLPHONE",
                params:{
                    mobileno: mobileno
                }
            },function(data){
            })
        } else {
            window.location.href = `tel:${mobileno}`;
        }
    }

    /**
     * 
     * @param op
     * @returns {string}
     */
    txtImg(op = {}){
        let fontSize = (op.fontSize||16)*2;
        let long = fontSize*(op.text.length);
        return `${TXTTOIMG}?width=${op.width|| long }&height=${op.height||(fontSize*1+5)}&color=${encodeURIComponent(op.color||"#000000")}&font=${encodeURIComponent(op.font||"")}&text=${encodeURIComponent(op.text||"")}&imgY=${op.imgY||fontSize}&imgX=${op.imgX||"0"}&fontSize=${fontSize}`;
    }
    
    myToast(str = "",showTime = 1500){
        if(str){
            let myToast = new MyToast();
            myToast.setToast(str,showTime)
        }
    }
}


