const CLOUDURL = "http://www.jslife.com.cn";   //
const JSPSNURL = "http://weixin.jslife.com.cn";   //H5

/*const CLOUDURL = "http://syx.jslife.com.cn";   //
 const JSPSNURL = "http://syxwx.jslife.com.cn";   //H5*/

/*const CLOUDURL = "http://jhtestcloud.jslife.net:18080";   //
 const JSPSNURL = "http://jhtestcloud.jslife.net";   //H5*/

/*const CLOUDURL = "http://testcloud-of.jslife.net";   // test code
const JSPSNURL = "http://jhtestcms.jslife.net";   //H5 test code*/

let BASEPATH = _basePath();
const XMPPSERVER = `${BASEPATH.jspsnURL}/XmppServer.servlet`;
const RSAENCRYPT = `${BASEPATH.jspsnURL}/RSAEncrypt.servlet`;
const WXGETPAYURL = `${BASEPATH.jspsnURL}/getPayUrl.servlet`;///URL
const REDIRECTMENU = `${BASEPATH.jspsnURL}/userValid.servlet`;//
const CARNOPAY = BASEPATH.cloudURL+"/qrcode/carNo-pay.html";  //
const TXTTOIMG = `${BASEPATH.jspsnURL}/txtToImg.servlet`;          //
const GETCITY = `http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&ak=fCRtA4ZvM8LpQh9luE8OjD5ipk0CYCih`;
const GETINVOICE = BASEPATH.jspsnURL+"/webapp/src/invoice/html/invoice.html";  //
const EXCLUDEPAGE =
    ["/ambitusPark/","bindTelphone",
    "couponList","map.html","successForCar.html",
    "successForCar.html","carNo-pay.html" ];  //
const DOWNLOADAPP = `${CLOUDURL}/jsstApp/jsp/version/downloadApp.html`;//APPURL
const GETATTENTION = `${BASEPATH.cloudURL}/pkextend`;// URL
export {
    XMPPSERVER,
    DOWNLOADAPP,
    RSAENCRYPT,
    EXCLUDEPAGE,
    WXGETPAYURL,
    REDIRECTMENU,
    CARNOPAY,
    TXTTOIMG,
    CLOUDURL,
    JSPSNURL,
    GETCITY,
    GETATTENTION,
    GETINVOICE,
    BASEPATH
}
/**
 * 
 * @returns {{}}
 */
function _basePath(){
    let path = {};
    if(window.location.href.indexOf("/jspsn/") > -1){
        path.jspsnURL = `${window.location.origin}/jspsn`;
    }else {
        path.jspsnURL = `${JSPSNURL}/jspsn`;
    }
    if(window.location.href.indexOf("/jsaims/") > -1){
        path.cloudURL  = `${window.location.origin}/jsaims`;
    }else {
        if(window.location.href.indexOf("/jsscan/") > -1){
            path.cloudURL  = `${window.location.origin}/jsscan`;
        }else {
            path.cloudURL = `${CLOUDURL}/jsaims`;
        }
    }
    return path;
}


